.btn {
	position: relative;

	@include adaptive-property(
		font-size,
		px-to-rem(14),
		px-to-rem(16),
		px-to-rem(18)
	);
	@include adaptive-property(
		padding,
		$input-paddings-sm,
		$input-paddings-md,
		$input-paddings-lg
	);

	@include montserrat-alternates-bold();
	text-transform: uppercase;
	text-align: center;
	text-decoration: none;

	border-radius: px-to-rem(30);

	&:not(:disabled) {
		cursor: pointer;
	}

	&_primary {
		&_outline {
			border: 1px solid #eee;

			&:hover, &:focus {
				border-color: #000EE9;
			}
		}

		background: gradient($gradient-white-start, $gradient-white-end);
		&:disabled {
			$darken-percentage: 30;
			background: gradient(
				darken($gradient-white-start, $darken-percentage),
				darken($gradient-white-end, $darken-percentage)
			);
			color: darken($light-text-color, $darken-percentage);
		}

		border: 0;
		outline: none;

		&, &_outline {
			display: flex;
			justify-content: center;
			align-items: center;

			color: $light-text-color;

			overflow: hidden;
			transition: all ease-in-out 0.6s;
			z-index: 100;

			&::before {
				content: " ";
				position: absolute;
				top: 0;
				left: 0;

				display: block;
				width: 100%;
				height: 100%;

				transition: all ease-in-out 0.6s;
				z-index: -100;

				background: gradient($gradient-blue-start, $gradient-blue-end);
				opacity: 0;
			}

			&:not(:disabled) {
				&:hover, &:focus {
					color: #fff;

					&::before {
						opacity: 1;
					}
				}

				&:hover {
					box-shadow: 0 5px 150px 11px #000EE9;
				}
			}
		}
	}

	&_blue {
		background: gradient($gradient-blue-start, $gradient-blue-end);
		color: #eee;
		transition: box-shadow 0.25s ease-out;

		&:focus, &:hover {
			box-shadow: inset 0 0 0 5px #eee;
		}
	}

	&_secondary-lg-circle {
		@mixin adaptive-btn-size($property) {
			@include adaptive-property(
				$property,
				px-to-rem(250),
				px-to-rem(300),
				px-to-rem(513)
			);
		}

		@mixin adaptive-font-size($property) {
			@include adaptive-property($property,
				px-to-rem(16),
				px-to-rem(20),
				px-to-rem(40)
			);
		}

		@include adaptive-btn-size(width);
		@include adaptive-btn-size(height);
		@include adaptive-property(
			padding,
			px-to-rem(20),
			px-to-rem(30),
			px-to-rem(70)
		);

		box-sizing: border-box;

		outline: 0;

		overflow: hidden;
		transition: all ease-in-out 0.6s;
		z-index: 100;

		@include montserrat-alternates-black();
		@include adaptive-font-size(font-size);
		@include adaptive-font-size(line-height);

		color: #fff;
		border: 1px solid #eee;
		border-radius: 100%;
		@include radial-primary-transparent-background($gradient-start-transparency: 0.5);

		&::before {
			content: " ";
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;

			transition: all ease-in-out 0.6s;
			z-index: -100;

			background: #eee;
			opacity: 0;
		}

		&:focus, &:hover {
			color: $light-text-color;
			border-color: transparent;
			&::before {
				opacity: 1;
			}
		}

		&:hover {
			transform: rotate(-6.07deg);
		}
	}

	&_circle {
		border-radius: 100% !important;
	}
}

a.btn {
	display: grid;
	place-items: center;
}
