.accent-bg-circle {
	--circle-width: 200px;
	--circle-height: var(--circle-width);
	width: var(--circle-width);
	max-width: 100%;
	height: var(--circle-height);

	content: " ";
	background: darken(#0016DA, 10%);
	// background: #0016DA;
	border-radius: 100%;
	filter: blur(100px);
	z-index: -1;
}

.circle-bg {
	--padding-top: 10px;
	--padding-bottom: 5px;
	--padding-left: -10px;
	--padding-right: -10px;
	--border: 1px solid #fff;
	--rotate: -5deg;

	position: relative;

	&::before {
		content: " ";
		position: absolute;
		top: var(--padding-top);
		bottom: var(--padding-bottom);
		left: var(--padding-left);
		right: var(--padding-right);
		transform: rotate(var(--rotate));
		border: var(--border);
		border-radius: 100%;
		z-index: -1;
	}
}

.card {
	border: $card-border;
	@include adaptive-property(
		border-radius,
		$card-border-radius-sm,
		$card-border-radius-sm,
		$card-border-radius-lg
	);
}
