@keyframes opacity-in {
	from { opacity: 1 }
	to { opacity: 0 }
}

@keyframes translate-y-in {
	from { transform: translateY(0) }
	to { transform: translateY(10px) }
}

.modal {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	z-index: 999;
	background: rgba(0, 0, 0, 0.5);
	backdrop-filter: blur(4px);

	display: none;
	opacity: 0;

	&.show {
		display: flex !important;
		animation: opacity-in 0.2s ease-in-out reverse both;
		.modal__window {
			animation: translate-y-in 0.3s ease-in-out reverse both;
		}
	}

	&__window {
		@include adaptive-property(
			width,
			60vw,
			50%,
			35%
		);
		margin: auto;

		display: flex;
		flex-direction: column;
		gap: px-to-rem(50);
		@include adaptive-property(
			gap,
			px-to-rem(30),
			px-to-rem(30),
			px-to-rem(50)
		);

		@include adaptive-property(
			padding,
			px-to-rem(20),
			px-to-rem(50),
			px-to-rem(70)
		);
		border-radius: px-to-rem(40);

		background: linear-gradient(140deg, rgba(23, 23, 23, 1) 0%, rgba(13, 14, 49, 1) 100%);
		border: 1px solid rgba(255, 255, 255, 0.25);
	}

	&__content {
		font-size: px-to-rem(24);
		text-align: center;

		display: flex;
		flex-direction: column;
		gap: px-to-rem(24);
	}

	&__footer {
		display: flex;
		justify-content: center;

		button {
			width: 100%;
		}
	}

	h2 {
		@include montserrat-alternates-bold();
		font-size: px-to-rem(32);

		@media #{$breakpoint-md-up} {
			font-size: px-to-rem(40);
		}
	}

	p {
		@include adaptive-property(
			font-size,
			px-to-rem(16),
			px-to-rem(16),
			px-to-rem(28)
		);
	}
}
