@import "sass-flexbox-grid/main";
@import "base";

* {
	margin: 0;
	padding: 0;
	box-sizing: content-box;
}

html {
	scroll-behavior: smooth;
	overflow-x: hidden;
}

body {
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	background: linear-gradient(140deg, rgba(23,23,23,1) 0%, rgba(13,14,49,1) 100%);
	background-color: $body-bg;

	// For debug purposes
	// @include adaptive-property(
	// 	background,
	// 	red,
	// 	orange,
	// 	green
	// );
}

.navbar {
	background-color: rgba(255, 255, 255, 0.02);
	backdrop-filter: blur(5px);
	-webkit-backdrop-filter: blur(5px);

	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 999 !important;

	&__container {
		@include row();
		@include adaptive-property(
			padding-top,
			px-to-rem(16),
			px-to-rem(12),
			px-to-rem(26)
		);
		@include adaptive-property(
			padding-bottom,
			px-to-rem(16),
			px-to-rem(12),
			px-to-rem(26)
		);
	}

	&__brand {
		display: flex;
		align-items: center;
		padding: 0 $gutter;
	}

	&__logo {
		display: flex;

		img {
			@include adaptive-property(
				height,
				px-to-rem(44),
				px-to-rem(48),
				px-to-rem(68)
			);
		}
	}

	&__content {
		@include row();
		@include col();
		align-items: center;

		.btn {
			margin-left: auto;
			padding: px-to-rem(15) px-to-rem(20) !important;
			font-size: px-to-rem(11) !important;

			@media #{$breakpoint-xs-only} {
				box-sizing: content-box;
			}

			@media #{$breakpoint-md-up} {
				margin-left: 0;
			}

			@media #{$breakpoint-lg-up} {
				padding: $input-paddings-lg !important;
				font-size: px-to-rem(18) !important;
			}
		}
	}

	&__links {
		margin: 0 0 0 auto;
		@media #{$breakpoint-md-up} {
			display: flex;
			margin: 0 auto;
		}

		display: none;
		list-style: none;

		@include adaptive-property(
			gap,
			px-to-rem(15),
			px-to-rem(30),
			px-to-rem(70)
		);
	}

	&__link-item {
		@include adaptive-property(
			width,
			auto,
			auto,
			px-to-rem(130)
		);
	}

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		@include adaptive-property(
			padding,
			px-to-rem(18) 0,
			px-to-rem(18) 0,
			px-to-rem(18) 0
		);

		@include montserrat-alternates-regular();
		@include adaptive-property(
			font-size,
			px-to-rem(12),
			px-to-rem(14),
			px-to-rem(18)
		);

		color: #fff;
		text-decoration: none;
		text-transform: uppercase;

		span {
			transition: all ease-out 0.3s;
		}

		@media #{$breakpoint-lg-up} {
			&:hover span {
				transform: scale(1.1) rotateZ(-3deg);
				font-weight: bold;
			}
		}
	}
}

header {
	position: relative;
	@include adaptive-property(
		margin-bottom,
		px-to-rem(74),
		px-to-rem(115),
		px-to-rem(55)
	);

	@include adaptive-property(
		padding-top,
		px-to-rem(126),
		px-to-rem(167),
		px-to-rem(199)
	);

	.container {
		@include row();
		@include row-flex-column();
		@include middle();
		gap: 5rem;
	}

	h1 {
		@include col(12);
		width: 100%;

		@include montserrat-alternates-regular();
		font-weight: 100;

		@include adaptive-property(
			font-size,
			px-to-rem(40),
			px-to-rem(50),
			px-to-rem(120)
		);
		line-height: 1;

		.big-line {
			display: flex;
			flex-direction: column;

			.hr {
				display: none;
			}

			span:last-child {
				margin-top: px-to-rem(34);
			}
		}

		@media #{$breakpoint-md-up} {
			.big-line {
				flex-direction: row;
				justify-content: space-between;
				margin-top: 0;

				.hr {
					display: block;
					content: " ";
					width: 100%;
					height: 1px;
					background: gradient();
					margin: auto px-to-rem(20);
					// margin: auto px-to-rem(10);
				}

				span:last-child {
					margin-top: 0;
					text-align: right;
				}
			}

			.right {
				display: flex;
				justify-content: flex-end;
			}
		}
	}

	.call-to-action {
		@include col(8);
		@include col("lg", 6);
		width: 100%;

		p {
			text-align: center;
			margin-bottom: px-to-rem(20);
			@include montserrat-regular();
			@include adaptive-property(
				font-size,
				px-to-rem(18),
				px-to-rem(18),
				px-to-rem(24)
			);
			opacity: 0.8;
		}

		.btn {
			@include adaptive-property(
				padding-top,
				px-to-rem(16),
				px-to-rem(18),
				px-to-rem(24)
			);
			@include adaptive-property(
				padding-bottom,
				px-to-rem(16),
				px-to-rem(18),
				px-to-rem(24)
			);
		}
	}

	.header-accent-bg-1 {
		position: absolute;
		top: -279px;

		@include adaptive-property(
			left,
			-335px,
			-500px,
			-500px
		);
	}

	.header-accent-bg-2 {
		position: absolute;
		top: -50px;

		@include adaptive-property(
			right,
			-80px,
			-50px,
			0
		);
	}
}

main {
	position: relative;
	display: flex;
	flex-direction: column;
	@include adaptive-property(
		margin-bottom,
		px-to-rem(150),
		px-to-rem(150),
		px-to-rem(200)
	);
}

section {
	position: relative;

	&:not(:first-child) {
		@include adaptive-property(
			padding-top,
			px-to-rem(110),
			px-to-rem(150),
			px-to-rem(200)
		);

		@include adaptive-property(
			margin-top,
			px-to-rem(40),
			0, 0
		);
	}

	h2 {
		@include col(12);
		text-align: center;
		@include adaptive-property(
			margin-bottom,
			px-to-rem(50),
			px-to-rem(50),
			px-to-rem(75)
		);
	}
}

section#video {
	position: relative;
	@include row();

	p {
		@include col(12);
		@include adaptive-property(
			font-size,
			px-to-rem(14),
			px-to-rem(16),
			px-to-rem(28)
		);
		@include adaptive-property(
			margin-bottom,
			px-to-rem(10),
			px-to-rem(10),
			px-to-rem(32)
		);
	}

	.video-container {
		@include col(12);

		display: flex;
		@include center();

		video {
			@include adaptive-property(
				width,
				100%,
				100%,
				auto
			);
			// width: 100%;
			// object-fit: contain;
			@include rounded();
			@include adaptive-property(
				height,
				auto,
				auto,
				px-to-rem(956)
			);
		}
	}
}

section#kse-nmt-is {
	@include row();
	flex-direction: column;

	h2 {
		margin-bottom: 0 !important;
	}

	.container {
		@include row();
		@include col(12);

		flex-direction: column;
		align-items: center;

		@media #{$breakpoint-lg-up} {
			@include row();
			@include around();
		}

		@include adaptive-property(
			gap,
			px-to-rem(62.5),
			px-to-rem(64),
			0
		);

		@include adaptive-property(
			margin-top,
			px-to-rem(89.25),
			px-to-rem(89.25),
			px-to-rem(144)
		);
	}

	.content-container {
		--header-img: url(/assets/images/emojis/image\ 30.png);

		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;

		@include adaptive-property(
			width,
			px-to-rem(300),
			px-to-rem(300),
			px-to-rem(512)
		);
		@include adaptive-property(
			height,
			px-to-rem(300),
			px-to-rem(300),
			px-to-rem(512)
		);

		border: 1px solid #fff;
		border-radius: 100%;
		box-sizing: border-box;
		padding: 3rem;

		@include radial-primary-transparent-background();

		p {
			text-align: center;
			margin: 0;
			@include adaptive-property(
				font-size,
				px-to-rem(14),
				px-to-rem(16),
				px-to-rem(28)
			);
			@include adaptive-property(
				line-height,
				px-to-rem(15),
				px-to-rem(18),
				px-to-rem(31)
			);
		}

		&::before {
			$image-padding: 8*2;
			$sm-size: px-to-rem(70 + $image-padding);
			$lg-size: px-to-rem(138 + $image-padding);

			@function calc-top-property($size) {
				@return calc($size * -1 / 2);
			}

			@function calc-left-property($size) {
				@return calc(50% - $size / 2);
			}

			content: " ";

			background: var(--header-img);
			background-size: cover;
			background-repeat: no-repeat;

			position: absolute;

			@include adaptive-property(
				top,
				calc-top-property($sm-size),
				calc-top-property($sm-size),
				calc-top-property($lg-size)
			);
			@include adaptive-property(
				left,
				calc-left-property($sm-size),
				calc-left-property($sm-size),
				calc-left-property($lg-size)
			);

			@include adaptive-property(width, $sm-size, $sm-size, $lg-size);
			@include adaptive-property(height, $sm-size, $sm-size, $lg-size);

			// filter: drop-shadow(0 -2px 47.5px #3D3D3D);
		}
	}
}

section#photos {
	@mixin images-gap() {
		@include adaptive-property(
			gap,
			px-to-rem(20),
			px-to-rem(23),
			px-to-rem(50)
		);
	}

	@include row();
	@include images-gap();
	justify-content: center;
	gap: px-to-rem(217.74);

	padding-left: $gutter;
	padding-right: $gutter;

	@media #{$breakpoint-md-up} {
		justify-content: initial;
		gap: 0;
	}

	img {
		object-fit: cover;
		@include rounded();

		@include adaptive-property(
			width,
			px-to-rem(328),
			px-to-rem(340),
			clamp(px-to-rem(646), 38vw, px-to-rem(719))
		);
		@include adaptive-property(
			height,
			px-to-rem(220),
			px-to-rem(220),
			auto
		);
	}

	.top {
		display: flex;
		flex-direction: column;
		@include images-gap();

		@media #{$breakpoint-md-only} {
			@include col(12);

			img {
				@include col(6);
			}
		}

		@media #{$breakpoint-md-up} {
			flex-direction: row;
			justify-content: center;
		}
	}

	.bottom {
		width: 100%;
		display: flex;
		align-items: flex-start;
		justify-content: center;
		position: relative;

		@media #{$breakpoint-md-only} {
			@include col(12);
			justify-content: flex-end;

			img {
				// @include col(6);
				padding-left: $gutter * 2;
			}
		}

		@media #{$breakpoint-lg-up} {
			justify-content: flex-end;
		}

		.btn {
			position: absolute;
			top: px-to-rem(-232);

			@media #{$breakpoint-md-up} {
				right: px-to-rem(337);
				top: px-to-rem(-46);
			}

			@media #{$breakpoint-lg-up} {
				// right: px-to-rem(600);
				right: 42%;
				top: px-to-rem(-72);
			}

			@media #{$breakpoint-xl-up} {
				right: 40%;
				// right: px-to-rem(700);
			}
		}
	}
}

section#yak-pobudovane-navchannya {
	@include row();

	h2 {
		@include adaptive-property(
			margin-top,
			0,
			px-to-rem(36),
			0
		);
		@include adaptive-property(
			margin-bottom,
			px-to-rem(50),
			px-to-rem(50),
			px-to-rem(68)
		);
	}

	.container {
		@include row();
		@include col(12);

		gap: px-to-rem(50);
		@media #{$breakpoint-md-up} {
			gap: 0;
		}

		.item {
			@include col(12);
			@include col("md", 6);
			@include col("lg", 4);

			$padding-vertical-sm: 0;
			$padding-horizontal-sm: px-to-rem(66);
			$padding-vertical-md: px-to-rem(40.57);
			$padding-horizontal-md: px-to-rem(37.5);
			$padding-vertical-lg: px-to-rem(180.5);
			// $padding-horizontal-lg: px-to-rem(95);
			$padding-horizontal-lg: px-to-rem(50);

			$image-width-sm: px-to-rem(70);
			$image-width-lg: px-to-rem(100);
			$image-width-hover: px-to-rem(80);

			display: flex;
			flex-direction: column;
			gap: px-to-rem(15);

			position: relative;

			@include adaptive-property(
				padding,
				$padding-vertical-sm $padding-horizontal-sm,
				$padding-vertical-md $padding-horizontal-md,
				$padding-vertical-lg $padding-horizontal-lg
			);

			border: 0;
			border-color: rgba(238, 238, 238, 0.25);
			border-style: solid;

			@media #{$breakpoint-md-only} {
				&:nth-child(odd):not(:nth-last-child(2)) {
					border-bottom-width: 1px;
				}
				&:nth-child(even) {
					border-left-width: 1px;

					&:not(:last-child) {
						border-bottom-width: 1px;
					}
				}
			}

			@media #{$breakpoint-lg-up} {
				&:first-child, &:nth-child(3), &:nth-child(2) {
					border-bottom-width: 1px;
				}

				&:nth-child(2), &:nth-child(5) {
					border-left-width: 1px;
					border-right-width: 1px;
				}
			}

			img {
				@include adaptive-property(width, $image-width-sm, $image-width-sm, $image-width-lg);
				@include adaptive-property(height, $image-width-sm, $image-width-sm, $image-width-lg);

				object-fit: contain;
				// filter: drop-shadow(0px 5px 52.5px #414141);
				margin: auto;
				transition: all ease-in-out 0.5s;
			}

			p {
				@include montserrat-alternates-bold();

				@include adaptive-property(
					font-size,
					px-to-rem(14),
					px-to-rem(16),
					px-to-rem(28)
				);
				@include adaptive-property(
					line-height,
					px-to-rem(15),
					px-to-rem(18),
					px-to-rem(31)
				);

				text-align: center;
				color: #eee;

				transition: all ease-in-out 0.5s;
			}

			@media #{$breakpoint-lg-up} {
				p {
					position: absolute;
					top: $padding-vertical-lg * 1.8;
					left: $padding-horizontal-lg;
					right: $padding-horizontal-lg;
					opacity: 0;
				}

				&:hover {
					img {
						width: $image-width-hover !important;
						height: $image-width-hover !important;
					}

					p {
						top: 65%;
						opacity: 1;
					}
				}
			}
		}
	}
}

section#nashi-vykladachi {
	@include row();
	margin: 0 !important;
	max-width: 100% !important;

	h2 {
		margin-bottom: 0;
	}

	.scrollable-container {
		@include col(12);
		overflow-x: auto;
		overflow-y: hidden;

		position: relative;
		@include adaptive-property(
			padding-top,
			px-to-rem(50),
			px-to-rem(50),
			px-to-rem(75)
		);

		@media #{$breakpoint-lg-up} {
			overflow-x: visible;
		}
	}

	.container {
		@include row();
		@include adaptive-property(
			gap,
			px-to-rem(16),
			px-to-rem(30),
			2vw
		);

		flex-wrap: nowrap;
		align-items: stretch;

		@media #{$breakpoint-lg-up} {
			&:hover {
				.item:not(:hover) {
					z-index: 1;
					& > * {
						opacity: 0.25;
					}

					&::before {
						backdrop-filter: blur(4px);
					}
				}
			}
		}
	}

	.item {
		display: flex;
		flex-direction: column;
		position: relative;
		z-index: 2;

		@include adaptive-property(
			gap,
			px-to-rem(7),
			px-to-rem(10),
			px-to-rem(10)
		);

		@include adaptive-property(
			min-width,
			px-to-rem(300),
			px-to-rem(300),
			px-to-rem(275)
		);

		@media #{$breakpoint-lg-up} {
			@include col();
		}

		& > * {
			transition: opacity 0.25s ease-in-out;
		}

		& > p {
			@include montserrat-alternates-bold();
			text-align: center;

			@include adaptive-property(
				font-size,
				px-to-rem(14),
				px-to-rem(14),
				px-to-rem(16)
			);
		}

		&::before {
			content: " ";
			display: block;
			position: absolute;

			// to make blur uncropped
			top: -10px;
			bottom: -10px;
			left: 0;
			right: 0;

			z-index: 1;
			transition: backdrop-filter 0.25s ease-in-out;
		}
	}

	.card {
		$border-width-sm: 3 * 2;
		$border-width-lg: 5;
		flex-grow: 1;

		box-sizing: border-box;

		@include adaptive-property(
			padding,
			px-to-rem(30 - $border-width-sm) px-to-rem(32),
			px-to-rem(30 - $border-width-sm) px-to-rem(30),
			px-to-rem(100 - $border-width-lg) px-to-rem(10) px-to-rem(50)
		);

		display: flex;
		flex-direction: column;
		align-items: center;

		.header {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			position: relative;

			img {
				border-radius: 100%;
				border-style: solid;
				border-color: #eee;

				@include adaptive-property(
					border-width,
					calc($border-width-sm / 2),
					calc($border-width-sm / 2),
					calc($border-width-lg / 2)
				);

				@include adaptive-property(
					height,
					px-to-rem(125),
					px-to-rem(125),
					px-to-rem(150)
				);
			}

			h3 {
				position: relative;
				text-align: center;

				@include adaptive-property(
					font-size,
					px-to-rem(20),
					px-to-rem(20),
					px-to-rem(30)
				);

				@include adaptive-property(
					margin,
					px-to-rem(15 - $border-width-sm) 0 px-to-rem(30 - $border-width-sm),
					px-to-rem(15 - $border-width-sm) 0 px-to-rem(30 - $border-width-sm),
					px-to-rem(16 - $border-width-lg) 0 px-to-rem(50 - $border-width-lg)
				)
			}

			.subtitle {
				@include montserrat-alternates-bold();
				margin-bottom: px-to-rem(10);

				@include adaptive-property(
					font-size,
					px-to-rem(14),
					px-to-rem(14),
					px-to-rem(16)
				);
			}

			.label {
				font-weight: bold;
				background-color: #eee;
				color: $light-text-color;
				border-radius: px-to-rem(20);

				@include adaptive-property(
					font-size,
					px-to-rem(14),
					px-to-rem(14),
					px-to-rem(16)
				);

				@include adaptive-property(
					padding,
					px-to-rem(10) px-to-rem(24),
					px-to-rem(10) px-to-rem(24),
					px-to-rem(16) px-to-rem(32)
				);
			}
		}

		.quote-container {
			display: flex;
			align-items: center;
			@include adaptive-property(
				height,
				px-to-rem(110),
				px-to-rem(128),
				px-to-rem(180)
			);

			p {
				text-align: center;
				color: #eee;
				@include adaptive-property(
					font-size,
					px-to-rem(12),
					px-to-rem(12),
					px-to-rem(16)
				);
			}
		}
	}
}

section#prohodte-nashi-kursi {
	@include row();

	.container {
		@include row();
		@include around();
		@include col(12);

		.item {
			@include col("lg", 7);
			@include col("xl", 6);
		}
	}

	.card {
		display: flex;
		flex-direction: column;

		@include adaptive-property(
			padding,
			0,
			px-to-rem(50),
			px-to-rem(100)
		);

		@include adaptive-property(
			gap,
			px-to-rem(30),
			px-to-rem(40),
			px-to-rem(50)
		);

		border-width: 0;
		@media #{$breakpoint-md-up} {
			border-width: 1px;
		}

		h3 {
			position: relative;
			display: inline-block;
			@include montserrat-alternates-bold();

			margin-bottom: px-to-rem(30);

			@media #{$breakpoint-lg-up} {
				text-align: center;
			}

			@include adaptive-property(
				font-size,
				px-to-rem(24),
				px-to-rem(30),
				px-to-rem(53)
			);

			.title-content {
				position: relative;

				span {
						position: absolute;

						@include adaptive-property(
							right,
							px-to-rem(-40),
							0,
							0
						);
						@include adaptive-property(
							bottom,
							px-to-rem(-34),
							px-to-rem(-36),
							px-to-rem(-42)
						);

						display: inline-block;
						transform: rotateZ(-1.59deg);

						@include adaptive-property(
							padding,
							px-to-rem(12) px-to-rem(22),
							px-to-rem(12) px-to-rem(22),
							px-to-rem(16) px-to-rem(32)
						);

						background: gradient($gradient-blue-start, $gradient-blue-end);
						border-radius: px-to-rem(20);

						@include montserrat-regular();
						font-weight: 400 !important;
						@include adaptive-property(
							font-size,
							px-to-rem(12),
							px-to-rem(14),
							px-to-rem(20)
						);
					}
			}
		}

		ul {
			@include montserrat-alternates-regular();
			@include adaptive-property(
				font-size,
				px-to-rem(14),
				px-to-rem(16),
				px-to-rem(20)
			);

			li {
				@mixin adaptive-size($property) {
					@include adaptive-property($property,
						px-to-rem(20),
						px-to-rem(25),
						px-to-rem(34)
					);
				}

				--marker: "";

				position: relative;
				list-style: none;
				line-height: 1.27;

				@include adaptive-property(
					padding-left,
					px-to-rem(20 + 7 + 4), // 4 is a magic number
					px-to-rem(25 + 7),
					px-to-rem(34 + 10 + 20)
				);

				&::before {
					content: " ";

					position: absolute;
					@include adaptive-property(
						left,
						px-to-rem(2),
						px-to-rem(0),
						px-to-rem(20)
					);
					@include adaptive-property(
						top,
						px-to-rem(-2),
						px-to-rem(0),
						px-to-rem(-5)
					);

					@include adaptive-size(width);
					@include adaptive-size(height);

					background-image: var(--marker);
					background-position: center center;
					background-size: contain;
					background-repeat: no-repeat;
				}

				&:not(:last-child) {
					@include adaptive-property(
						margin-bottom,
						px-to-rem(15),
						px-to-rem(25),
						px-to-rem(34.5)
					);
				}
			}
		}
	}
}

section#zapys-na-probnyy-urok {
	@include row();

	.container {
		@include row();
		@include center();
	}

	.content {
		@include col();
		@include col("lg", 7);
		@include col("xl", 6);

		p:first-child {
			@include montserrat-regular();
			font-weight: bold;
			@include adaptive-property(
				font-size,
				px-to-rem(20),
				px-to-rem(20),
				px-to-rem(28)
			);
		}

		ul, p {
			@include montserrat-alternates-regular();
			@include adaptive-property(
				font-size,
				px-to-rem(14),
				px-to-rem(16),
				px-to-rem(20)
			);
			line-height: 1.22;
		}

		ul {
			margin-top: px-to-rem(15);
			margin-bottom: px-to-rem(10);
			margin-left: px-to-rem(25);
		}
	}

	form {
		margin-top: px-to-rem(50);

		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: repeat(4, 1fr);
		@include adaptive-property(
			gap,
			px-to-rem(25),
			px-to-rem(30),
			px-to-rem(30)
		);

		input, button {
			width: 100%;
			box-sizing: border-box;
		}

		@media #{$breakpoint-md-up} {
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(3, 1fr);

			#name { grid-area: 1 / 1 / 2 / 2; }
			#tel { grid-area: 1 / 2 / 2 / 3; }
			#email { grid-area: 2 / 1 / 3 / 3; }
			#submit { grid-area: 3 / 1 / 4 / 3; }
		}
	}
}

footer {
	.container {
		@include row();

		border-top: 1px solid rgb(238, 238, 234, 0.25);

		@include adaptive-property(
			padding-top,
			px-to-rem(25),
			px-to-rem(50),
			px-to-rem(75),
		);

		@include adaptive-property(
			padding-bottom,
			px-to-rem(40),
			px-to-rem(26.43),
			px-to-rem(87)
		);

		@include adaptive-property(
			gap,
			px-to-rem(50),
			px-to-rem(50),
			0
		);

		& > .maps {
			@include col("xs", 12);
			@include col("sm", 12);
			@include col("md", 12);
			@include col("lg", 5);
			@include col("xl", 5);

			min-height: px-to-rem(472);

			iframe {
				width: 100%;
				height: 100%;
				box-shadow: 0 5px 100px #C9C9C9;
				border-radius: px-to-rem(20);
			}
		}

		& > .content {
			@include col-offset("lg", 1);
			@include col();

			@include montserrat-alternates-regular();

			display: flex;
			flex-direction: column;
			@include adaptive-property(
				gap,
				px-to-rem(50),
				px-to-rem(50),
				px-to-rem(100)
			);

			.contacts {
				display: flex;
				flex-direction: column;

				@include adaptive-property(
					gap,
					px-to-rem(15),
					px-to-rem(15),
					px-to-rem(30)
				);

				p {
					@include adaptive-property(
						font-size,
						px-to-rem(14),
						px-to-rem(16),
						px-to-rem(28)
					);

					text-align: center;

					@media #{$breakpoint-lg-up} {
						text-align: left;
					}
				}

				a {
					@include montserrat-alternates-bold();
					text-decoration: none;
					color: #eee;
				}
			}

			.socials {
				display: flex;

				justify-content: center;

				@media #{$breakpoint-lg-up} {
					justify-content: flex-start;
				}

				@include adaptive-property(
					gap,
					px-to-rem(20),
					px-to-rem(20),
					px-to-rem(30)
				);

				.btn {
					@mixin adaptive-btn-size($property) {
						@include adaptive-property(
							$property,
							px-to-rem(50),
							px-to-rem(70),
							px-to-rem(100)
						);
					}

					@include adaptive-btn-size(width);
					@include adaptive-btn-size(height);
					padding: 0 !important;

					img {
						@include adaptive-property(
							height,
							px-to-rem(20),
							px-to-rem(28),
							px-to-rem(40)
						);

						object-fit: contain;
					}
				}
			}

			.powered-by {
				display: flex;
				flex-direction: column;
				align-items: center;

				@media #{$breakpoint-lg-up} {
					align-items: flex-start;
				}

				img {
					max-width: 100%;
					object-fit: contain;
				}

				p {
					@include adaptive-property(
						margin-top,
						px-to-rem(10),
						px-to-rem(10),
						px-to-rem(20)
					);
					@include adaptive-property(
						font-size,
						px-to-rem(12),
						px-to-rem(12),
						px-to-rem(20)
					);
				}
			}
		}
	}
}

.call-btn {
	position: fixed;
	right: 3rem;
	bottom: 3rem;
	padding: 0 !important;
	z-index: 999;

	$btn-size-sm: px-to-rem(50);
	$btn-size-lg: px-to-rem(80);

	$icon-size-sm: px-to-rem(20);
	$icon-size-lg: px-to-rem(40);

	@include adaptive-property(
		width,
		$btn-size-sm,
		$btn-size-sm,
		$btn-size-lg
	);
	@include adaptive-property(
		height,
		$btn-size-sm,
		$btn-size-sm,
		$btn-size-lg
	);

	img {
		transition: height 0.25s ease-out;
		@include adaptive-property(
			height,
			$icon-size-sm,
			$icon-size-sm,
			$icon-size-lg
		);
	}

	&:hover img {
		@include adaptive-property(
			height,
			px-to-rem(15),
			px-to-rem(15),
			px-to-rem(35)
		);
	}
}
