@font-face {
	font-family: "Montserrat";
	font-weight: 400 700;
	font-display: swap;
	src: url(/assets/fonts/Montserrat-VariableFont_wght.woff2) format("woff2"),
		 url(/assets/fonts/Montserrat-VariableFont_wght.ttf) format("truetype");
}

@font-face {
	font-family: "MontserratAlternates-Light";
	font-weight: 200;
	font-display: swap;
	src: url(/assets/fonts/subset/montserratalternates-light-webfont.woff2) format("woff2"),
		 url(/assets/fonts/subset/montserratalternates-light-webfont.ttf) format("truetype");
}

@font-face {
	font-family: "MontserratAlternates-Regular";
	font-weight: 400;
	font-display: swap;
	src: url(/assets/fonts/subset/montserratalternates-regular-webfont.woff2) format("woff2"),
		 url(/assets/fonts/subset/montserratalternates-regular-webfont.ttf) format("truetype"),
}

@font-face {
	font-family: "MontserratAlternates-Bold";
	font-weight: 700;
	font-display: swap;
	src: url(/assets/fonts/subset/montserratalternates-bold-webfont.woff2) format("woff2"),
		 url(/assets/fonts/subset/montserratalternates-bold-webfont.ttf) format("truetype");
}

@font-face {
	font-family: "MontserratAlternates-Black";
	font-weight: 900;
	font-display: swap;
	src: url(/assets/fonts/subset/montserratalternates-black-webfont.woff2) format("woff2"),
		 url(/assets/fonts/subset/montserratalternates-black-webfont.ttf) format("truetype");
}

html {
	@include adaptive-property(
		font-size,
		62.5%,
		62.5%,
		clamp(6px, 0.6vw, 10px)
	);
}

@mixin montserrat-regular() {
	font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

@mixin montserrat-alternates-light() {
	font-family: "MontserratAlternates-Light", Helvetica, Arial, sans-serif;
	font-weight: 200;
}

@mixin montserrat-alternates-regular() {
	font-family: "MontserratAlternates-Regular", Helvetica, Arial, sans-serif;
}

@mixin montserrat-alternates-bold() {
	font-family: "MontserratAlternates-Bold", "Arial Black", Helvetica, Arial, sans-serif;
	font-weight: 700;
}

@mixin montserrat-alternates-black() {
	font-family: "MontserratAlternates-Black", "Arial Black", Helvetica, Arial, sans-serif;
	font-weight: 900;
}

body {
	@include montserrat-regular();
	color: #fff;
	@include adaptive-property(
		font-size,
		1.6rem,
		1.6rem,
		clamp(1.1rem, 0.7vw, 1.6rem)
	);
}

h1, h2, h3, h4, h5, h6, strong, b, .font-black {
	@include montserrat-alternates-black();
}

h2 {
	font-size: px-to-rem(30);
	line-height: 1;
	text-shadow: 0 5px 52.5px #525252;

	@media #{$breakpoint-md-up} {
		font-size: px-to-rem(40);
	}

	@media #{$breakpoint-lg-up} {
		font-size: px-to-rem(80);
	}
}

.text-right {
	text-align: right;
}

.font-light {
	@include montserrat-alternates-light();
}

.font-bold {
	@include montserrat-alternates-bold();
}
