@use 'sass:math';

@function gradient(
	$color-start: $gradient-white-start,
	$color-end: $gradient-white-end,
	$deg: -45deg
) {
	@return linear-gradient($deg, $color-start, $color-end);
}

@function px-to-rem($px, $bodySize: 10) {
	@return math.div($px, $bodySize) * 1rem;
}

@mixin adaptive-property(
	$property,
	$sm-value,
	$md-value: "",
	$lg-value: ""
) {
	#{$property}: $sm-value;

	@if ($md-value != "") {
		@media #{$breakpoint-md-up} {
			#{$property}: $md-value !important;
		}
	}

	@if ($lg-value != "") {
		@media #{$breakpoint-lg-up} {
			#{$property}: $lg-value !important;
		}
	}
}

@mixin rounded() {
	@include adaptive-property(
		border-radius,
		px-to-rem(30),
		px-to-rem(30),
		px-to-rem(60)
	);
}

@mixin radial-primary-transparent-background(
	$transparent-percent: 50%,
	$gradient-start-transparency: 1
) {
	background: rgb(27, 22, 153);
	background: radial-gradient(
		circle,
		rgba(27, 22, 153, $gradient-start-transparency) 0%,
		transparent $transparent-percent
	);
}
